
import { defineComponent, ref, onMounted, provide } from 'vue';
import Panel from '../core/Panel';
import CCTree from './tree/tree.vue';
import CCInspector from './inspector.vue';
import { NodeInfo } from './tree';
import { Provide } from './index';
import { bus, Event } from './tree/index';
import TButton from '../rich-text-editor/comp/btn.vue';
import { Msg } from './msg';

const EditorPanel = 'editor';
export default defineComponent({
  name: 'app',
  components: { CCTree, CCInspector, TButton },
  setup(props, { emit }) {
    const tree = ref([] as NodeInfo[]);
    const panel = new Panel('inspector');
    provide(
      Provide.PropertyChange,
      (nodeID: string, property: string, value: any, refresh = false) => {
        const data = { nodeID, property, value };
        panel.dispatch({
          panel: EditorPanel,
          action: 'property-change',
          data,
          response: () => {
            if (refresh) {
              updateNodeInfo();
            }
          },
        });
      },
    );
    onMounted(async () => {
      await panel.init();
      updateTreeData();
    });
    let curSelectNodeID = '';

    function updateTreeData() {
      panel.dispatch({
        panel: EditorPanel,
        action: Msg.MsgGetSceneChildrenInfo,
        data: {},
        response: (data: any) => {
          tree.value = data.data;
        },
      });
    }

    function updateNodeInfo() {
      if (curSelectNodeID) {
        panel.dispatch({
          panel: EditorPanel,
          action: Msg.MsgGetNodeSelfInfo,
          data: { nodeID: curSelectNodeID },
          response: (data: any) => {
            inspect.value = data.data;
          },
        });
      }
    }

    function requestChildren(nodeID: number): Promise<NodeInfo[]> {
      return new Promise((resolve, reject) => {
        panel.dispatch({
          panel: EditorPanel,
          action: Msg.MsgGetNodeChildrenInfo,
          data: { nodeID },
          response: (data: any) => {
            resolve(data.data);
          },
        });
      });

    }

    const inspect = ref<NodeInfo>();
    return {
      inspect,
      tree,
      onTest() {
        bus.emit(Event.Reset);
        updateTreeData();
      },
      async getChildren(node: NodeInfo) {
        const ret = await requestChildren(node.id);
        return ret;
      },
      onSelectTreeItem(data: any) {
        curSelectNodeID = data.id;
        updateNodeInfo();
      },
    };
  },
});
